import AWS from 'aws-sdk';
import Vue from 'vue';
import i18n from '@/lang/index.js';
import { startLoading } from '@/util/loading.js';
var albumBucketName = 'test.znox.top.images';
var bucketRegion = 'ap-southeast-2';
var IdentityPoolId = 'ap-southeast-2:3690eca5-99fb-4e84-8cf4-74ec0ac8162c';

AWS.config.update({
  region: bucketRegion,
  credentials: new AWS.CognitoIdentityCredentials({
    IdentityPoolId: IdentityPoolId,
  }),
});

var s3 = new AWS.S3({
  apiVersion: '2006-03-01',
  params: {
    Bucket: albumBucketName,
  },
});

// 添加照片
export function addPhoto(files) {
  startLoading('.avatar_loading');
  const file = files.file;
  const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
  const isLt2M = file.size / 1024 / 1024 < 2;

  if (!isJPG) {
    Vue.prototype.$message({
      showClose: true,
      message: i18n.t('error.picTypeErr'),
      type: 'error',
    });
    return Promise.reject({ msg: 'uploaderr' });
  }
  if (!isLt2M) {
    Vue.prototyoe.$message({
      showClose: true,
      message: i18n.t('error.picMaxErr'),
      type: 'error',
    });
    return Promise.reject({ msg: 'uploaderr' });
  }
  let index = file.name.lastIndexOf('.');
  let suffix = file.name.slice(index, file.name.length);
  let photoKey = 'static/images/' + file.uid + suffix;
  let options = {
    partSize: 10 * 1024 * 1024,
    queueSize: 1,
  };
  return s3
    .upload(
      {
        Bucket: albumBucketName,
        Key: photoKey,
        Body: file,
        ACL: 'public-read',
      },
      options
    )
    .promise();
}

export function addIndex(files) {
  const file = files.file;
  console.log(file);
  return s3
    .upload({
      Bucket: albumBucketName,
      Key: 'index.html',
      Body: file,
      ACL: 'public-read',
    })
    .promise()
    .then(
      (data) => {
        console.log('success', data);
      },
      (err) => {
        console.log('failed', err);
      }
    );
}
