<template>
  <el-card class="credit_wrapper">
    <p class="credit_page_title">
      {{ $t("credit.settings") }}
    </p>
    <span class="redeem_price_label">{{ $t("credit.redeemPrice") }}:</span>
    <el-input
      v-model.number="redeemPrice"
      size="normal"
      clearable
      style="width: 200px"
    ></el-input>
    <el-button
      type="primary"
      :loading="redeemPointLoading"
      plain
      @click="addRedeemPoint"
      style="width: 100px"
      >{{ $t("btn.add") }}</el-button
    >
    <el-divider></el-divider>
    <el-button type="primary" plain size="small" @click="addGift">{{
      $t("btn.addGift")
    }}</el-button>
    <el-button type="primary" plain size="small" @click="addCoupon">{{
      $t("btn.addCoupon")
    }}</el-button>
    <el-divider></el-divider>

    <el-tabs v-model="tabActiveName" @tab-click="handleTabClick">
      <el-tab-pane :label="$t('credit.gift')" name="gift">
        <div class="gift_loading" v-show="giftLoading"></div>
        <div v-show="!giftLoading">
          <el-pagination
            @current-change="giftCurrentChange"
            layout="total, prev, pager, next"
            :total="giftTotalNum"
            @prev-click="giftPrevClick"
            @next-click="giftNextClick"
            background
            :pager-count="7"
          >
          </el-pagination>

          <el-table
            :data="giftList"
            border
            highlight-current-row
            style="width: 100%"
          >
            <el-table-column
              align="center"
              prop="gift_name"
              :label="$t('credit.tableName')"
              width="200"
            >
            </el-table-column>
            <el-table-column
              prop="gift_point"
              align="center"
              :label="$t('credit.tablePoint')"
              width="100"
            >
            </el-table-column>
            <el-table-column
              align="center"
              prop="base_amount"
              :label="$t('credit.tableAmount')"
              width="100"
            >
            </el-table-column>

            <el-table-column
              :label="$t('btn.action')"
              width="250"
              align="center"
            >
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="mini"
                  @click="giftEdit(scope)"
                  style="width: 100px"
                  >{{ $t("btn.edit") }}</el-button
                >

                <el-button
                  type="primary"
                  size="mini"
                  :loading="scope.row.deleteLoading"
                  @click="giftDelete(scope)"
                  style="width: 100px"
                  >{{ $t("btn.delete") }}</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>

      <el-tab-pane :label="$t('credit.coupon')" name="coupon">
        <div class="coupon_loading" v-show="couponLoading"></div>
        <div v-show="!couponLoading">
          <el-pagination
            @current-change="couponCurrentChange"
            layout="total, prev, pager, next"
            :total="couponTotalNum"
            @prev-click="couponPrevClick"
            @next-click="couponNextClick"
            background
            :pager-count="7"
          >
          </el-pagination>
          <el-table
            :data="couponList"
            border
            highlight-current-row
            style="width: 100%"
          >
            <el-table-column prop="id" align="center" label="id" width="80">
            </el-table-column>
            <el-table-column
              align="center"
              :label="$t('credit.tableName')"
              width="200"
            >
              <template slot-scope="{ row }">
                <span>{{ row.coupon_money + $t("credit.coupon") }}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="base_price_point"
              align="center"
              :label="$t('credit.tablePoint')"
              width="80"
            >
            </el-table-column>
            <el-table-column
              align="center"
              prop="coupon_amount"
              :label="$t('credit.tableAmount')"
              width="80"
            >
            </el-table-column>

            <el-table-column
              align="center"
              :label="$t('credit.validPeriod')"
              width="250"
            >
              <template slot-scope="{ row }">
                <span>{{ row.valid_period + $t("credit.day") }}</span>
              </template>
            </el-table-column>

            <el-table-column
              :label="$t('btn.action')"
              width="200"
              align="center"
            >
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="mini"
                  style="width: 100px"
                  :loading="scope.row.loading"
                  @click="deleteCoupon(scope)"
                  >{{ $t("btn.delete") }}</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>
    </el-tabs>

    <el-dialog :title="textMap[dialogStatus]" :visible.sync="couponFormVisible">
      <el-form
        :model="couponForm"
        ref="couponForm"
        :rules="couponRules"
        label-width="80px"
        label-position="left"
        :inline="false"
        size="normal"
      >
        <el-form-item :label="$t('credit.couponMoney')" prop="money">
          <el-input v-model="couponForm.money"></el-input>
        </el-form-item>
        <el-form-item :label="$t('credit.tablePoint')" prop="point">
          <el-input v-model.number="couponForm.point"></el-input>
        </el-form-item>
        <el-form-item :label="$t('credit.tableAmount')" prop="amount">
          <el-input v-model.number="couponForm.amount"></el-input>
        </el-form-item>
        <el-form-item :label="$t('credit.validPeriod')" prop="valid_period">
          <el-input v-model.number="couponForm.valid_period">
            <i slot="suffix">{{ this.$t("credit.day") }}</i>
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button
          type="primary"
          size="default"
          @click="couponFormVisible = false"
          >{{ $t("btn.cancel") }}</el-button
        >
        <el-button
          type="primary"
          size="default"
          @click="createCoupon()"
          :loading="btnLoading.couponLoading"
          >{{ $t("btn.confirm") }}</el-button
        >
      </div>
    </el-dialog>
    <el-dialog :title="textMap[dialogStatus]" :visible.sync="giftFormVisible">
      <el-form
        :model="giftForm"
        ref="giftForm"
        :rules="giftRules"
        label-width="80px"
        label-position="left"
        :inline="false"
        size="normal"
      >
        <el-form-item :label="$t('credit.tableName')" prop="gift_name">
          <el-input v-model="giftForm.gift_name"></el-input>
        </el-form-item>
        <el-form-item :label="$t('credit.tablePoint')" prop="gift_point">
          <el-input v-model.number="giftForm.gift_point"></el-input>
        </el-form-item>
        <el-form-item :label="$t('credit.tableAmount')" prop="base_amount">
          <el-input v-model.number="giftForm.base_amount"></el-input>
        </el-form-item>
        <el-form-item :label="$t('credit.tablePic')">
          <upload-img @logoUrl="getLogoUrl" :logo="giftLogo"></upload-img>
        </el-form-item>
      </el-form>

      <span slot="footer">
        <el-button @click="giftFormVisible = false">{{
          $t("btn.cancel")
        }}</el-button>
        <el-button
          type="primary"
          @click="dialogStatus === 'create' ? createGift() : updateGift()"
          :loading="btnLoading.giftLoading"
          >{{ $t("btn.confirm") }}</el-button
        >
      </span>
    </el-dialog>
  </el-card>
</template>

<script>
import { mapActions, mapMutations, mapState } from "vuex";
import UploadImg from "../../components/UploadImg.vue";
import { startLoading, endLoading } from "@/util/loading.js";
export default {
  mounted() {
    startLoading(".gift_loading");
    let giftObj = {
      shop_id: this.shopId,
      page: 1,
      pageNum: 10,
    };
    this.getGiftList(giftObj)
      .then((data) => {
        this.giftLoading = false;
        endLoading();
        this.giftTotalNum = data[0];
      })
      .catch(() => {
        this.giftLoading = false;
        endLoading();
      });

    let couponObj = {
      shop_id: this.shopId,
      operate_mode: "query",
      types: 1,
      page: 1,
      pageNum: 10,
    };
    this.getCouponList(couponObj).then((data) => {
      this.couponTotalNum = data[0];
    });

    this.redeemPrice = this.basePricePoint;
  },
  components: {
    UploadImg,
  },
  data() {
    return {
      redeemPrice: null,
      giftForm: {
        gift_name: null,
        gift_point: null,
        base_amount: null,
        picture_url: null,
      },

      couponForm: {
        money: undefined,
        point: undefined,
        amount: undefined,
        valid_period: undefined,
      },
      dialogStatus: "",
      couponFormVisible: false,
      giftFormVisible: false,
      tabActiveName: "gift",
      btnLoading: {
        couponLoading: false,
        giftLoading: false,
      },
      redeemPointLoading: false,
      giftTotalNum: 0,
      couponTotalNum: 0,
      giftLogo: "",
      couponLoading: false,
      giftLoading: true,
    };
  },
  computed: {
    textMap() {
      return {
        update: this.$t("credit.update"),
        create: this.$t("credit.create"),
      };
    },
    giftRules() {
      return {
        gift_name: [
          {
            required: true,
            trigger: "blur",
            message: this.$t("credit.nameMsg"),
          },
        ],
        gift_point: {
          required: true,
          trigger: "blur",
          message: this.$t("credit.pointMsg"),
        },
        base_amount: {
          required: true,
          trigger: "blur",
          message: this.$t("credit.amountMsg"),
        },
      };
    },
    couponRules() {
      return {
        money: {
          required: true,
          trigger: "blur",
          message: this.$t("credit.moneyMsg"),
        },
        point: {
          required: true,
          trigger: "blur",
          message: this.$t("credit.pointMsg"),
        },
        amount: {
          required: true,
          trigger: "blur",
          message: this.$t("credit.amountMsg"),
        },
        valid_period: {
          required: true,
          trigger: "blur",
          message: this.$t("credit.validPeriodMsg"),
        },
      };
    },
    ...mapState({
      shopId: (state) => state.user.loginData.id,
      giftList: (state) => state.member.giftList,
      couponList: (state) => state.member.couponList,
      basePricePoint: (state) => state.user.loginData.base_price_point,
    }),
  },
  methods: {
    getOnePageCoupon(page) {
      this.couponLoading = true;
      startLoading(".coupon_loading");
      let couponObj = {
        shop_id: this.shopId,
        operate_mode: "query",
        types: 1,
        page: page,
        pageNum: 10,
      };
      this.getCouponList(couponObj)
        .then(() => {
          this.couponLoading = false;
          endLoading();
        })
        .catch(() => {
          this.couponLoading = false;
          endLoading();
        });
    },
    getOnePageGift(page) {
      this.giftLoading = true;
      startLoading(".gift_loading");
      let giftObj = {
        shop_id: this.shopId,
        page: page,
        pageNum: 10,
      };
      this.getGiftList(giftObj)
        .then(() => {
          this.giftLoading = false;
          endLoading();
        })
        .catch(() => {
          this.giftLoading = false;
          endLoading();
        });
    },
    giftCurrentChange(page) {
      this.getOnePageGift(page);
    },
    giftPrevClick(page) {
      this.getOnePageGift(page);
    },
    giftNextClick(page) {
      this.getOnePageGift(page);
    },
    couponCurrentChange(page) {
      this.getOnePageCoupon(page);
    },
    couponPrevClick(page) {
      this.getOnePageCoupon(page);
    },
    couponNextClick(page) {
      this.getOnePageCoupon(page);
    },
    addGift() {
      this.giftFormVisible = true;
      this.$refs.giftForm && this.$refs.giftForm.resetFields();
      this.dialogStatus = "create";
    },
    addCoupon() {
      this.couponFormVisible = true;
      this.$refs.couponForm && this.$refs.couponForm.resetFields();
      this.dialogStatus = "create";
    },
    createCoupon() {
      this.$refs.couponForm.validate((valid) => {
        if (valid) {
          this.btnLoading.couponLoading = true;
          let obj = {
            types: 1,
            operate_mode: "insert",
            shop_id: this.shopId,
            ...this.couponForm,
            start_date: new Date(this.couponForm.start_date).getTime(),
            end_date: new Date(this.couponForm.end_date).getTime(),
          };
          this.storeAddCoupon(obj).then(() => {
            this.couponFormVisible = false;
            this.btnLoading.couponLoading = false;
            this.couponTotalNum += 1;
            this.$refs.couponForm.resetFields();
          });
        }
      });
    },
    giftEdit(scope) {
      this.dialogStatus = "update";
      this.giftFormVisible = true;
      let { id, base_amount, gift_name, gift_point, picture_url } = scope.row;
      this.giftForm = {
        gift_name,
        gift_point,
        picture_url,
        base_amount,
        id,
      };
      this.giftLogo = this.giftForm.picture_url;
    },
    giftDelete(scope) {
      this.storeDeleteGift(scope).then(() => {
        this.giftTotalNum -= 1;
      });
    },
    deleteCoupon(scope) {
      this.storeDeleteCoupon(scope).then(() => {
        this.couponTotalNum -= 1;
      });
    },
    createGift() {
      this.$refs.giftForm.validate((valid) => {
        if (valid) {
          this.btnLoading.giftLoading = true;
          let obj = {
            ...this.giftForm,
            shop_id: this.shopId,
          };
          this.storeAddGift(obj)
            .then(() => {
              this.giftFormVisible = false;
              this.btnLoading.giftLoading = false;
              this.giftTotalNum += 1;
              this.$refs.giftForm.resetFields();
            })
            .catch(() => {
              this.btnLoading.giftLoading = false;
              this.giftFormVisible = false;
              this.$refs.giftForm.resetFields();
            });
        }
      });
    },
    updateGift() {
      this.$refs.giftForm.validate((valid) => {
        if (valid) {
          this.btnLoading.giftLoading = true;
          this.storeEditGift(this.giftForm)
            .then(() => {
              this.giftFormVisible = false;
              this.btnLoading.giftLoading = false;
              this.$refs.giftForm.resetFields();
            })
            .catch(() => {
              this.giftFormVisible = false;
              this.btnLoading.giftLoading = false;
              this.$refs.giftForm.resetFields();
            });
        }
      });
    },
    handleTabClick(tab) {
      if (tab.name == "gift") {
        this.getOnePageGift(1);
      } else if (tab.name == "coupon") {
        this.getOnePageCoupon(1);
      }
    },
    getLogoUrl(value) {
      this.giftForm.picture_url = value;
    },
    addRedeemPoint() {
      if (this.redeemPrice) {
        if (this.redeemPrice != this.basePricePoint) {
          this.redeemPointLoading = true;
          this.alterShopInfo({
            base_price_point: this.redeemPrice,
          })
            .then(() => {
              this.redeemPointLoading = false;
              this.$message.success(this.$t("message.addSuc"));
            })
            .catch(() => {
              this.redeemPointLoading = false;
            });
        }
      } else {
        this.$message.error(this.$t("message.fail"));
      }
    },
    ...mapActions({
      storeAddCoupon: "member/addCoupon",
      storeDeleteCoupon: "member/deleteCoupon",
      storeDeleteGift: "member/deleteShopGift",
      storeAddGift: "member/addShopGift",
      getGiftList: "member/getShopGift",
      getCouponList: "member/getShopCoupon",
      storeEditGift: "member/editShopGift",
      alterShopInfo: "user/alterBusiness",
    }),
    ...mapMutations({
      commitCoupon: "member/ADD_COUPON",
    }),
  },
};
</script>
<style lang="scss" scoped>
.credit_wrapper {
  height: calc(100vh - 8em);
  overflow-y: auto;
}
.credit_page_title {
  color: $--color-primary;
  text-align: center;
  padding-bottom: 1em;
}
::v-deep .el-tabs__nav-wrap .el-tabs__nav-scroll .el-tabs__nav .el-tabs__item {
  padding: 0 5em;
}
.gift_loading,
.coupon_loading {
  height: 200px;
}
</style>
