<template>
  <el-upload
    class="avatar-uploader"
    action=""
    :show-file-list="false"
    :http-request="uploadFile"
  >
    <div class="avatar_loading" v-show="avatarLoading"></div>
    <div v-show="!avatarLoading" class="avatar_wrapper">
      <img :src="imgUrl ? imgUrl : ''" class="avatar" />
      <i class="el-icon-plus avatar-uploader-icon"></i>
      <div slot="tip" class="el-upload__tip">
        {{ $t("public.picInfo") }}
      </div>
    </div>
  </el-upload>
</template>

<script>
import { addPhoto } from "@/util/aws.config.js";
import { endLoading } from "@/util/loading.js";
export default {
  props: ["className", "logo"],
  created() {
    this.imgUrl = this.logo;
  },
  data() {
    return {
      avatarLoading: false,
      imgUrl: "",
    };
  },
  watch: {
    logo: function(value) {
      this.imgUrl = value;
    },
  },
  methods: {
    uploadFile(file) {
      this.avatarLoading = true;
      addPhoto(file).then(
        (data) => {
          this.avatarLoading = false;
          endLoading();
          this.imgUrl = data.Location;
          this.$emit("logoUrl", this.imgUrl);
        },
        (err) => {
          this.avatarLoading = false;
          endLoading();
          if (!err.msg) {
            this.$message.error(this.$t("error.picUploadErr"));
          }
        }
      );
    },
  },
};
</script>

<style lang="scss" scoped>
