  border: 1px dashed hsl(0, 0%, 85%);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}

.avatar_wrapper {
  position: relative;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
}

.avatar {
  width: 178px;
  height: 178px;
  display: block;
}

.avatar_loading {
  height: 178px;
  width: 178px;
}
</style>
